import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { CommodityService } from '../commodity.service';

@Component({
  selector: 'app-commodity',
  templateUrl: './commodity.component.html',
  styleUrls: ['./commodity.component.css']
})
export class CommodityComponent implements OnInit {
  commodities:any;
  addCommodity = new FormGroup({
    commodities: new FormControl("")
  });
  editForm = new FormGroup({
    commodity_id:new FormControl(""),
    commodities : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:CommodityService) { }

  ngOnInit() {
    let token = localStorage.getItem('token');
    var body = {"token":token};
    let tokenJson = JSON.stringify(body);
    this.service.fetchCommodity(body).subscribe(
      res => {
        this.commodities = res['valueList'];
      }
    );
  }
  onSubmit(){
    console.log(this.addCommodity.value);
    this.addCommodity.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addCommodity.value);
    this.service.addCommodity(bodyJson).subscribe(
      res => {
        if(res['commodity_id']){
          console.log("success")
        }
        else{
          console.log("error")
        }
        location.reload();
      }
    );
  }
  openModal(region){
    this.editForm.controls['commodity_id'].setValue(region.commodity_id);
    this.editForm.controls['commodities'].setValue(region.commodities);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteCommodity(body).subscribe(
      res=>{
        location.reload();
        // console.log(res);
      }
    );
  }

}
