import { Component, OnInit } from '@angular/core';
import {   FormGroup, FormControl } from '@angular/forms';
import { RegionService } from '../region.service';

@Component({
  selector: 'app-region',
  templateUrl: './region.component.html',
  styleUrls: ['./region.component.css']
})
export class RegionComponent implements OnInit {
  regions:any;
  addRegion = new FormGroup({
    countries: new FormControl("")
  });
  editForm = new FormGroup({
    country_id:new FormControl(""),
    countries : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:RegionService) { }

  ngOnInit() {
    let token = localStorage.getItem('token');
    var body = {"token":token};
    let tokenJson = JSON.stringify(body);
    this.service.fetchRegion(body).subscribe(
      res => {
        this.regions = res['valueList'];
      }
    );

  }
  onSubmit(){
    console.log(this.addRegion.value);
    this.addRegion.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addRegion.value);
    this.service.addRegion(bodyJson).subscribe(
      res => {
        if(res['country_id']){
          console.log("success")
        }
        else{
          console.log("error")
        }
        location.reload();
      }
    );
    
  }
  openModal(region){
    this.editForm.controls['country_id'].setValue(region.country_id);
    this.editForm.controls['countries'].setValue(region.countries);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteRegion(body).subscribe(
      res=>{
        location.reload();
        // console.log(res);
      }
    );
  }

}
