import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { DisruptionService } from '../disruption.service';

@Component({
  selector: 'app-disruption',
  templateUrl: './disruption.component.html',
  styleUrls: ['./disruption.component.css']
})
export class DisruptionComponent implements OnInit {
  disruptions:any;
  addDisruption = new FormGroup({
    sc_disruption_value: new FormControl("")
  });
  editForm = new FormGroup({
    sc_disruption_value_id:new FormControl(""),
    sc_disruption_value : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:DisruptionService) { }

  ngOnInit() {
    let token = localStorage.getItem('token');
    var body = {"token":token};
    let tokenJson = JSON.stringify(body);
    this.service.fetchDisruption(body).subscribe(
      res => {
        this.disruptions = res['valueList'];
      }
    );

  }
  onSubmit(){
    console.log(this.addDisruption.value);
    this.addDisruption.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addDisruption.value);
    this.service.addDisruption(bodyJson).subscribe(
      res => {
        if(res['sc_disruption_value_id']){
          console.log("success")
        }
        else{
          console.log("error")
        }
        location.reload();
      }
    );
    
  }
  openModal(region){
    this.editForm.controls['sc_disruption_value_id'].setValue(region.sc_disruption_value_id);
    this.editForm.controls['sc_disruption_value'].setValue(region.sc_disruption_value);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteDisruption(body).subscribe(
      res=>{
        location.reload();
        // console.log(res);
      }
    );
  }

}
