import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { MovingFactorService } from '../moving-factor.service';

@Component({
  selector: 'app-moving-factor',
  templateUrl: './moving-factor.component.html',
  styleUrls: ['./moving-factor.component.css']
})
export class MovingFactorComponent implements OnInit {
  movingFactors:any;
  addMovingFactor = new FormGroup({
    moving_factors: new FormControl("")
  });
  editForm = new FormGroup({
    moving_factor_id:new FormControl(""),
    moving_factors : new FormControl(""),
    status : new FormControl(""),
  });
  
  
  constructor(private service:MovingFactorService) { }

  ngOnInit() {
    let token = localStorage.getItem('token');
    var body = {"token":token};
    let tokenJson = JSON.stringify(body);
    this.service.fetchMovingFactor(body).subscribe(
      res => {
        this.movingFactors = res['valueList'];
      }
    );
  }
  onSubmit(){
    console.log(this.addMovingFactor.value);
    this.addMovingFactor.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addMovingFactor.value);
    this.service.addMovingFactor(bodyJson).subscribe(
      res => {
        if(res['moving_factor_id']){
          console.log("success")
        }
        else{
          console.log("error")
        }
        location.reload();
      }
    );
  }
  openModal(region){
    this.editForm.controls['moving_factor_id'].setValue(region.moving_factor_id);
    this.editForm.controls['moving_factors'].setValue(region.moving_factors);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteMovingFactor(body).subscribe(
      res=>{
        location.reload();
        // console.log(res);
      }
    );
  }

}
