import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { MovingFactorService } from '../moving-factor.service';
import { SubCategoryService } from '../sub-category.service';

@Component({
  selector: 'app-sub-category',
  templateUrl: './sub-category.component.html',
  styleUrls: ['./sub-category.component.css']
})
export class SubCategoryComponent implements OnInit {
  subCategories:any;
  addSubCategory = new FormGroup({
    sub_categories: new FormControl("")
  });
  editForm = new FormGroup({
    sub_category_id:new FormControl(""),
    sub_categories : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:SubCategoryService) { }

  ngOnInit() {
    let token = localStorage.getItem('token');
    var body = {"token":token};
    let tokenJson = JSON.stringify(body);
    this.service.fetchSubCategory(body).subscribe(
      res => {
        this.subCategories = res['valueList'];
      }
    );
  }
  onSubmit(){
    console.log(this.addSubCategory.value);
    this.addSubCategory.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addSubCategory.value);
    this.service.addSubCategory(bodyJson).subscribe(
      res => {
        if(res['sub_category_id']){
          console.log("success")
        }
        else{
          console.log("error")
        }
        location.reload();
      }
    );
  }
  openModal(region){
    this.editForm.controls['sub_category_id'].setValue(region.sub_category_id);
    this.editForm.controls['sub_categories'].setValue(region.sub_categories);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteSubCategory(body).subscribe(
      res=>{
        location.reload();
        // console.log(res);
      }
    );
  }

}
