import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { FactorValueService } from '../factor-value.service';

@Component({
  selector: 'app-factor-value',
  templateUrl: './factor-value.component.html',
  styleUrls: ['./factor-value.component.css']
})
export class FactorValueComponent implements OnInit {

  factors:any;
  addFactor = new FormGroup({
    factor_value: new FormControl("")
  });
  editForm = new FormGroup({
    factor_value_id:new FormControl(""),
    factor_value : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:FactorValueService) { }

  ngOnInit() {
    let token = localStorage.getItem('token');
    var body = {"token":token};
    let tokenJson = JSON.stringify(body);
    this.service.fetchFactorValue(body).subscribe(
      res => {
        this.factors = res['valueList'];
      }
    );

  }
  onSubmit(){
    console.log(this.addFactor.value);
    this.addFactor.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addFactor.value);
    this.service.addFactorValue(bodyJson).subscribe(
      res => {
        if(res['factor_value_id']){
          console.log("success")
        }
        else{
          console.log("error")
        }
        location.reload();
      }
    );
  }
  openModal(region){
    this.editForm.controls['factor_value_id'].setValue(region.factor_value_id);
    this.editForm.controls['factor_value'].setValue(region.factor_value);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteFactorValue(body).subscribe(
      res=>{
        location.reload();
        // console.log(res);
      }
    );
  }
}
